import React, { Component } from "react";
import { OverlayTrigger, Tooltip, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import { kFormatter } from "../../../../../utils";
import { connect } from 'react-redux';
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import SkActiveUsers from "./SkeletonComponents/SkActiveUsers";
import Author from "../../CommonComponents/Author/Author";
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from "../../../../../utils";
import withRouter from "../../../../routes/WithRouter";

class ActiveUsers extends Component{
    render(){
        const { topCreator, loader } = this.props;
        return <Card className="card-stretch gutter-b">
                <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                    <span className="h5 mb-0">Most Active Creators</span>
                    <OverlayTrigger placement='top' overlay={<Tooltip>Multiply your social UGC by collaborating with your top listed Creators</Tooltip>} >
                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                    </OverlayTrigger>
                </div>
                <Card.Body>
                    <div className="pe-2" style={{height:300, overflowX:'auto'}}>
                    {loader ? 
                        <SkActiveUsers/>
                    : 
                    topCreator?.data?.length > 0 ? topCreator.data.map((Item, Index) => <div key={Index} className="d-flex align-items-center px-3 py-2 btn-active-light cursor-pointer">
                        <div className="d-flex align-items-center me-0 me-sm-2 mb-3 mb-sm-0 flex-grow-1 justify-content-between w-100 cursor-pointer">
                            <div className='d-flex align-items-center'>
                                    <Author imageSize={40} 
                                        symbolClass={'symbol-label fs-6'} 
                                        className={'symbol symbol-38 rounded-circle flex-shrink-0 me-2 overflow-hidden'} 
                                        name={Item.author.name} profileUrl={Item.image.replace(/\s/g, '')} networkId={Item.network.id} />

                                    <div className="pe-2 w-100 overflow-hidden">
                                        <p className=" mb-0"><strong className="text-break text-ellipsis w-100 d-block">{Item.author.name}</strong></p>
                                        <small className="mb-0 text-muted text-break">{kFormatter(Item.postCount)} {Item.postCount && parseInt(Item.postCount) > 1 ? `Posts` : `Post`}</small>
                                    </div>
                                </div>
                            </div>
                            <div className="f-between flex-grow-1 w-100">
                                <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(Item.network.id)}</Tooltip>}>
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(Item.network.id)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(Item.network.id)} data-invert={GET_NETWORK_NAME(Item.network.id)} />
                                    </OverlayTrigger>
                                </span>
                                <Link to={`/creators${Item.author?.username ? '?text='+Item.author.username :''}`} className="btn btn-primary btn-sm btn-min-w">
                                    Connect
                                </Link>
                            </div>
                        </div> 
                        ) :
                        <NoCardData title="Find your Creators" desc="Nothing to see? Partner with top UGC creators with our Creator Collaborator program." methodTitle="" method={''} />
                        }
                    </div>
                </Card.Body>
            </Card>
    }
}

const mapStateToProps = state => {
    return {
        topCreator: state.dashboardData.top_creator,
        loader: state.dashboardData.top_creator_loader
    }
}

export default connect(mapStateToProps, null)(withRouter(ActiveUsers))